import * as Yup from "yup";

export const editProductFormValidation = Yup.object({
  name: Yup.string()
    .min(4, "     4 ")
    .max(70, "     70 ")
    .required("  "),
  price: Yup.number()
    .min(1, "    1")
    .required("  "),
  discount: Yup.number()
    .min(0, "     0")
    .max(100, "     100"),
  stock: Yup.number().min(1, "    1"),
  wight: Yup.string()
    .min(1, "     1 ")
    .max(20, "     20 "),
  pictures: Yup.string().url("  URL"),
  description: Yup.string()
    .min(4, "     4 ")
    .max(500, "     500 ")
    .required("  "),
});


