import * as Yup from "yup";

export const SignupFormValidation = Yup.object({
  email: Yup.string()
    .email("   ")
    .required("  "),
  group: Yup.string()
    .max(3, "  3 ")
    .required("  "),
  password: Yup.string().required("  ")
    .min(4, "     4 ")
    .max(8, "     8 ")
    .matches(/[0-9]/, "    ."),
});
